body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Drop {
    float: left;
    padding: 0 10px 20px 10px;
    width: 12.5%;
}

@media (min-width: 2400px) {
    .Drop { width: calc(100% / 12); }
}

@media (min-width: 1200px) and (max-width: 2399px) {
    .Drop { width: calc(100% / 8); }
}

@media (min-width: 980px) and (max-width: 1199px) {
    .Drop { width: calc(100% / 5); }
}

@media (min-width: 768px) and (max-width: 979px) {
    .Drop { width: calc(100% / 4); }
}

@media (min-width: 480px) and (max-width: 767px) {
    .Drop { width: calc(100% / 3); }
}

@media (max-width: 479px) {
    .Drop { width: calc(100% / 2); }
}

.Drop .DropBorder {
    border: 1px solid black;
    border-radius: 5px;
    /*margin: 10px;*/
    overflow: auto;
    width: 100%;
}

.Drop .DropContent {
    padding: 10px;
}

.Drop .DropIconBox {
    margin-bottom: 10px;
    padding-top: 110%;
    position: relative;
    width: 100%;
}

.Drop .DropIcon {
    cursor: pointer;
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

.Drop .DropIndicator {
    font-size: 18px;
    font-weight: bold;
    left: 10px;
    pointer-events: none;
    position: absolute;
    text-shadow: -1px 0 #FFF, 0 1px #FFF, 1px 0 #FFF, 0 -1px #FFF;
    top: 10px;
}

.Drop .DropQuantity {
    bottom: 10px;
    font-size: 16px;
    font-weight: bold;
    pointer-events: none;
    position: absolute;
    right: 10px;
    text-shadow: -1px 0 #FFF, 0 1px #FFF, 1px 0 #FFF, 0 -1px #FFF;
}

.Drop .DropInput {
    margin-bottom: 10px;
    text-align: center;
}
.Node .DropsContainer {
    margin: 20px -10px 0 -10px;
}
.Event {

}

.Event .NodeSelector {
    display: block;
    margin-bottom: 20px;
}

.App {
    padding: 10px 20px;
    position: relative;
}

.App .EventSelector {
    display: block;
    margin-bottom: 20px;
}

.App .AppTitle {
    position: relative;
}

.App .AppTitle>h1>small {
    font-size: 16px;
}

.App .AppSettingsToggle {
    bottom: 5px;
    position: absolute;
    right: 0;
}
